<template>
  <div>
    <nav-bar>
      <template v-slot:default>用户注册</template>
    </nav-bar>

    <div style="margin-top: 45px;">
      <div style="text-align: center;">
        <van-image
          width="10rem"
          height="10rem"
          fit="contain"
          src="https://cdn2.lmonkey.com/94f152aaa94d937ccf5de78f3fcac59f/3b6e32e7bc8145a283431f260c3c1d1a.png"
        />
      </div>

      <van-form @submit="onSubmit">
        <van-field
          v-model="name"
          name="用户名"
          label="用户名"
          placeholder="用户名"
          :rules="[{ required: true, message: '请填写用户名' }]"
        />
        <van-field
          v-model="password"
          type="password"
          name="密码"
          label="密码"
          placeholder="密码"
          :rules="[{ required: true, message: '请填写密码' }]"
        />
        <van-field
          v-model="password_confirmation"
          type="password"
          name="密码"
          label="密码"
          placeholder="确认密码"
          :rules="[{ required: true, message: '请确认密码' }]"
        />
        <van-field
          v-model="email"
          name="电子邮箱"
          label="电子邮箱"
          placeholder="请输入正确电子邮箱格式"
          :rules="[{ required: true, message: '请填写电子邮箱' }]"
        />
        <div style="margin: 16px;">
          <div class="link-login" @click="$router.push({path:'/login'})">
            已有账号，立即登录
          </div>
          <van-button round block type="info" color="#42b983" native-type="submit">注册</van-button>
        </div>
      </van-form>
    </div>
  </div>
</template>

<script>
  import NavBar from "components/common/navbar/NavBar";
  import {register} from "network/user";
  import { Notify, Toast } from 'vant';
  import {ref, reactive, toRefs} from 'vue';
  import {useRouter} from 'vue-router';

  export default {
    name: "Register",
    components: {
      NavBar
    },
    setup(){
      const router = useRouter();

      const userInfo = reactive({
        name: '',
        password: '',
        password_confirmation: '',
        email: ''
      })

      const onSubmit = ()=>{
         //先验证两次密码是否一致
         if(userInfo.password != userInfo.password_confirmation){
           Notify('两次密码不一致！')
         }else{
           //提交给服务器
           register(userInfo).then(res=>{
             console.log(res);
             if(res.status == '201'){
               Toast.success('注册成功');

               setTimeout(()=>{
                 router.push({path:'/login'})
               },1000)

               userInfo.password_confirmation = '';
               userInfo.password = '';
             }
           })
         }
      }

      return {
        ...toRefs(userInfo),
        onSubmit
      }
    }
  }
</script>

<style scoped>
.link-login {
  font-size: 14px;
  color: #3a7ef3;
  margin-bottom: 20px;
  /*display: inline-block;*/
  float: left;
}
</style>